
.v_header {
  width: 100%;
  height: auto;
  background: url('v_banner.jpg') no-repeat center 0px;
  background-size: cover;
  color: #fff;
}
.v_container {
  width: 100%;
  height: auto;
  background: url('v_dott.png') repeat;
}
.v_banner {
  max-width: 1200px;
  padding: 2rem 0;
  margin: 0 auto;
}
.v_headpic {
  float: left;
  width: 40%;
  text-align: center;
}
.v_headpic img {
  width: 74%;
  border: 5px solid rgba(255, 255, 255, 0.68);
}
.v_baseinfo {
  float: right;
  width: 60%;
  text-align: left;
}
.v_baseinfo h5 {
  position: relative;
  display: inline-block;
  padding: 0.1rem 0.5rem;
  font-size: 0.4rem;
  color: #fff;
  background-color: #03a9f4;
  font-family: 'Overlock', cursive;
  vertical-align: top;
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
/*小三角*/
.v_baseinfo h5:after {
  position: absolute;
  width: 0;
  height: 0;
  left: 0.24rem;
  bottom: -0.14rem;
  content: '';
  border-style: solid;
  border-width: 0.2rem 0.2rem 0 0.2rem;
  border-color: #03a9f4 transparent;
}
h1 {
  font-size: .6rem;
  font-family: 'Overlock', cursive;
}
h6 {
  margin-top: .2rem;
  font-size: .4rem;
  font-family: 'Overlock', cursive;
  color: #C7C7C7;
  letter-spacing: .1rem;
}
.v_baseinfo ul {
  margin-top: .3rem;
  border-top: 1px solid #fff;
}
.v_baseinfo ul li {
  font-size: .3rem;
  margin-top: .5rem;
  list-style: none;
}
.v_baseinfo b {
  display: inline-block;
  font-family: 'Overlock', cursive;
  margin-right: .5rem;
}
.v_baseinfo ul li span:hover {
  color: #03a9f4;
}
/*清除浮动*/
.clearfix:before {
  display: table;
  content: "";
  box-sizing: border-box;
}
.clearfix:after {
  clear: both;
  display: table;
  content: "";
  box-sizing: border-box;
}



